<template>
    <v-col cols="12">
        <qiong-title-element class="mt-4 qiong-pt-10">
            非活跃下线
        </qiong-title-element>

        <div class="qiong-pt-15 mb-5">
            <div class="qiong-tr qiong-txt-tr">
                <div class="qiong-wide-10">&nbsp;</div>
                <div class="qiong-wide-11">會員號碼</div>
                <div class="qiong-wide-35">會員名稱</div>
                <div class="qiong-wide-13">层数</div>
                <div class="qiong-wide-15">支付级别</div>
                <div class="qiong-wide-11">訂單點數</div>
                <div class="qiong-wide-5">&nbsp;</div>
            </div>

            <qiong-space :space="'0.2vw'"></qiong-space>
            <qiong-space :space="'0.4vh'"></qiong-space>

            <div v-if="buying">
                <div class="qiong-td"
                    v-for="(v, i) in buying" :key="i"
                >
                    <div class="qiong-wide-10">&nbsp;</div>
                    <div class="qiong-wide-11">{{ v.users_permissions_user.number_code }}</div>
                    <div class="qiong-wide-35">{{ v.users_permissions_user.display_name }}</div>
                    <div class="qiong-wide-13">{{ v.user_sponser.index - me.index }}</div>
                    <div class="qiong-wide-15">
                        <level-viewing :item="v.user_sponser.vip.named + '_' + v.user_sponser.vip.star"></level-viewing>
                    </div>
                    <div class="qiong-wide-11">{{ v.product.pv * v.num }}</div>
                    <div class="qiong-wide-5">&nbsp;</div>
                </div>

                <div class="qiong-pt-20 pb-3 empty-content" v-if="buying.length < 0">
                    <qiong-empty :bigger="0"></qiong-empty>
                </div>
            </div>
            <div class="qiong-pt-20 pb-3 empty-content" v-else>
                <qiong-loading></qiong-loading>
            </div>

        </div>

        <div class="qiong-td">
            <div class="qiong-wide-10"></div>
            <div class="qiong-wide-84 pr-0">
                
                <qiong-sub-result :res="result"></qiong-sub-result>
            </div>
            <div class="qiong-wide-6"></div>
        </div>
    </v-col>
</template>

<script>
import QiongTitleElement from '../../../../components/Qiong/Ele/QiongTitleElement.vue'
import QiongSubResult from '../../../../components/Qiong/Pager/QiongSubResult.vue'
import QiongPageLing from '../../../../components/Qiong/QiongPageLing.vue'
import QiongEmpty from '../../../../components/Qiong/Ui/QiongEmpty.vue'
import QiongLoading from '../../../../components/Qiong/Ui/QiongLoading.vue'
import QiongSpace from '../../../../components/Qiong/Ui/QiongSpace.vue'
import LevelViewing from '../../../../components/Viewing/LevelViewing.vue'
    export default {
        components: {
            QiongTitleElement,
                QiongPageLing,
                QiongEmpty,
                QiongSpace,
                QiongLoading,
                QiongSubResult,
                LevelViewing 
        },
        props: {
            me: {
                type: Object
            },
            childs: {
                type: Array
            }
        },
        name: '',
        data() {
            return {
                buying: null,
                pv_Limit: 100,

                unLive: [ ],
                total: 0
            }
        },
        mounted() {
            this.pv_Limit = this.bonus.conf.USER_PV_LIMIT
            this.buyingLoading()
        },
        computed: {
            result() {
                let res = [ ]

                if (this.me) {
                    res = [
                        {
                            txt: '非活跃人数',
                            content: this.unLive.length
                        },
                        {
                            txt: '订单点数总和',
                            content: this.total
                        }
                    ]
                }

                return res
            }
        },
        methods: {
        
            buyingLoading() {

                this.buying = [ ]
                this.unLive = [ ]
                this.childs.map(async e => {
                    
                    if (e.ipv < this.pv_Limit) {

                        let res = await this.conn.get(
                            this.api.buys,
                            this.$store.state.token,
                            {
                                'chronu': this.$store.state.chronus.id,
                                'users_permissions_user': e.id,
                                '_sort': 'published_at:DESC'
                            }
                        )

                        if (res) {
                            res.map(j => {
                                j.user_sponser = e
                                this.buying.push(j)

                                this.total += (j.v.product.pv * v.num)
                            })

                            this.unLive.push(e)
                        }

                        console.log(e)
                    }
                })
            }
        },
    }
</script>

<style lang="sass" scoped>
.empty-content
    // min-height: calc( 270px + 5vw )
</style>


