<template>
    <v-col cols="12">
        <qiong-title-element :extra_class="'qiong-txt-primary'">
            活躍分店消費獎勵
        </qiong-title-element>

        <div class="qiong-pt-15 mb-5">
            <div class="qiong-tr qiong-txt-tr">
                <div class="qiong-wide-10">&nbsp;</div>
                <div class="qiong-wide-11">會員號碼</div>
                <div class="qiong-wide-40">會員名稱</div>
                <div class="qiong-wide-10">層數</div>
                <div class="qiong-wide-13">訂單號碼</div>
                <div class="qiong-wide-11">訂單點數</div>
                <div class="qiong-wide-5">&nbsp;</div>
            </div>

            <qiong-space :space="'0.2vw'"></qiong-space>
            <qiong-space :space="'0.4vh'"></qiong-space>

            <div v-if="buying.length > 0">
                <div class="qiong-td"
                    v-for="(v, i) in buying" :key="i"
                >
                    <div class="qiong-wide-10">&nbsp;</div>
                    <div class="qiong-wide-11">{{ v.users_permissions_user.number_code }}</div>
                    <div class="qiong-wide-40">{{ v.users_permissions_user.display_name }}</div>
                    <div class="qiong-wide-10">{{ v.user_sponser.index - me.index }}</div>
                    <div class="qiong-wide-13">{{ v.order_code }}</div>
                    <div class="qiong-wide-11">{{ v.product.pv * v.num }}</div>
                    <div class="qiong-wide-5">&nbsp;</div>
                </div>
            </div>
            <div class="qiong-pt-20 pb-3 empty-content" v-else>
                <qiong-loading v-if="loading"></qiong-loading>
                <qiong-empty :bigger="0" v-else></qiong-empty>
            </div>

        </div>

        <div class="qiong-td">
            <div class="qiong-wide-10"></div>
            <div class="qiong-wide-84 pr-0">
                
                <qiong-sub-result :res="result"></qiong-sub-result>
            </div>
            <div class="qiong-wide-6"></div>
        </div>
    </v-col>
</template>

<script>
import QiongTitleElement from '../../../../components/Qiong/Ele/QiongTitleElement.vue'
import QiongSubResult from '../../../../components/Qiong/Pager/QiongSubResult.vue'
import QiongPageLing from '../../../../components/Qiong/QiongPageLing.vue'
import QiongEmpty from '../../../../components/Qiong/Ui/QiongEmpty.vue'
import QiongLoading from '../../../../components/Qiong/Ui/QiongLoading.vue'
import QiongSpace from '../../../../components/Qiong/Ui/QiongSpace.vue'
    export default {
        components: {
            QiongTitleElement,
                QiongPageLing,
                QiongEmpty,
                QiongSpace,
                QiongLoading,
                QiongSubResult
        },
        props: {
            me: {
                type: Object
            },
            childs: {
                type: Array
            }
        },
        name: '',
        data() {
            return {
                buying: [ ],
                ratios: [ ],

                total: 0,
                loading: true
            }
        },
        mounted() {
            
            this.buyingLoading()
        },
        computed: {
            result() {
                let res = [ ]

                if (this.me) {
                    res = [
                        {
                            txt: '活躍分店消費獎勵：',
                            content: `US$ ${ this.me.bonus.usd_active_house }`
                        },
                        {
                            txt: '訂單點數總和：',
                            content: `${ this.total }`
                        }
                    ]
                }

                return res
            }
        },
        methods: {
        
            buyingLoading() {

                this.total = 0
                this.buying = [ ]
                this.childs.map(async e => {

                    if (e.index - this.me.index <= 2) {
                        let res = await this.conn.get(
                            this.api.buys,
                            this.$store.state.token,
                            {
                                'chronu': this.$store.state.chronus.id,
                                'users_permissions_user': e.id,
                                '_sort': 'published_at:DESC'
                            }
                        )

                        if (res) {
                            res.map(j => {
                                j.user_sponser = e
                                this.buying.push(j)

                                this.total = this.view.floatAdd(this.total, ( j.product.pv * j.num ))
                            })
                        }
                    }
                })

                this.loading = false
            }
        },
    }
</script>

<style lang="sass" scoped>
.empty-content
    // min-height: calc( 270px + 5vw )
</style>


